.footer {
  position: relative;
  width: 100%;
  padding: 0 17%;
  height: 20vh;
  min-height: 150px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #f9f9f9;
  user-select: none;
  /* background: #F3AE3E; */
}

.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 30%;
  height: .4vw;
  min-height: 5px;
  background: #333;
  border-radius: 50px;
}

