.footer p {
  padding: 0;
  font-size: 16px;
  line-height: 16px;
  color: #333;
}

.footer .logo {
  z-index: 1;
  pointer-events: none;
  width: 1.7vw;
  height: 1.7vw;
  min-width: 40px;
  min-height: 40px;
}

.footer .logo::before {
  background: #222;
}

.pgs-cprght {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.pgs-cprght p {
  margin-left: 1vw;
}

@media screen and (max-width: 1080px) {
  .footer {
    padding: 0 11.5%;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 0 10%;
  }

  .footer::before {
    width: 30%;
  }
}

@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
    padding: 0 5%;
  }

  .footer .logo {
    display: none;
  }

}