.panel {
  height: 100%;
  min-height: 100vh;
}

.panel h1 {
  font-size: 1.8em;
  font-weight: 300;
  margin: 0 auto;
}

.panel p,
.panel li {
  text-align: left;
  margin: 0.3em 0 1em 0;
}

.panel p code,
.panel li code {
  background-color: rgba(255, 255, 255, 0.15);
  padding: 2px 4px;
  border-radius: 5px;
}

.panel.description {
  padding-bottom: 60px;
}

.panel {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  top: 0;
  left: 0;
}

.blue {
  flex-direction: row;
  user-select: none;
  /* pointer-events: none; */
}

#wel--co--me {
  position: relative;
  overflow: hidden;
  width: 100%;
  /* min-height: calc(400px + 17vw); */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  cursor: none;
}

#wel--co--me::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #212347;
  transform: translateX(0%);
}

#wel--co--me.act::before {
  transform: translateX(0%);
  transition: 1.2s cubic-bezier(0.7, 0, 0.3, 1);
}

#wel--co--me .tr--24 {
  position: absolute;
  height: 100%;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
}

#wel--co--me .tr--24 h2,
#wel--co--me .tr--24 h3 {
  color: #f7f5e9;
  pointer-events: none;
  text-align: center;
  /* white-space: nowrap; */
  user-select: none;
  transform: translateY(12vw);
}

#wel--co--me .tr--24 h2 {
  color: #f7f5e9;
  -webkit-text-stroke: calc(2px + .1vw) #f7f5e9;
}

#wel--co--me .tr--24 h4 {
  color: #EBDDBD;
}

.orange img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.purple {
  background-color: #c4bb6b;
}

/* .gray {
  background-color: #777;
} */

.line {
  width: 100%;
  max-width: 1400px;
  height: 20px;
  margin-top: 20px;
  position: relative;
  z-index: 7;
  display: inline-block;
  background-color: rgba(255, 255, 255, 1);
}

.wel-h2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 12%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  z-index: 1;
  overflow: hidden;
}

.quotes {
  position: absolute;
  width: 50%;
  left: 15%;
}

.quotes h2 {
  position: relative;
  /* font-size: calc(23px + 3.6vw);
  line-height: calc(18px + 2.9vw); */
  font-size: calc(23px + 3.7vw);
  line-height: calc(18px + 2.9vw);
  letter-spacing: -0.1vw;
  color: #c54a37;
  font-family: "FuturaIWS";
  cursor: default;
  text-align: left;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: 0;
  left: 20%;
  cursor: none;
  -webkit-text-stroke: calc(1px + .1vw) #c54a37;
}

.quotes h2 span {
  color: #222;
  -webkit-text-stroke: calc(1px + .1vw) #222;
}

.quotes p {
  position: relative;
  font-family: "FuturaIWS";
  font-size: 1.3vw;
  line-height: 0;
  font-weight: lighter;
  margin-top: 1.5vw;
  opacity: 0;
  color: #222;
  left: 10%;
}

.tr--1 {
  position: absolute;
  width: 100% !important;
  height: 100% !important;
  z-index: 1;
  top: 0;
  right: -48%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1.2);
  opacity: 0;
}

.tr--1 svg {
  position: absolute;
  width: 105%;
  height: 105%;
  object-fit: cover;
}

.st0 {
  fill: #c54a37;
}

.st1 {
  fill: #F3AE3E;
}

.st2 {
  fill: #EBDDBD;
}

.st3 {
  fill: #519ac2;
}

.st4 {
  fill: #212347;
}

.st5 {
  fill: url(#SVGID_1_);
}

.st6 {
  fill: url(#SVGID_2_);
}

.st7 {
  fill: url(#SVGID_3_);
}

.st8 {
  fill: url(#SVGID_4_);
}

.st9 {
  fill: url(#SVGID_5_);
}

.act-h .tr--1 svg .cir-anim {
  animation: 3s tr1circle cubic-bezier(0.7, 0, 0.3, 1) infinite;
  transform-origin: 88% 25%;
}

.act-h .tr--1 svg .cir-anim2 {
  animation: 3s tr1circle cubic-bezier(0.7, 0, 0.3, 1) infinite 1s;
  transform-origin: 90% 30%;
}

.act-h .tr--1 svg .cir-anim3 {
  animation: 3s tr1circle cubic-bezier(0.7, 0, 0.3, 1) infinite;
  transform-origin: 33% 100%;
}

.act-h .tr--1 svg .cir-anim4 {
  animation: 3s tr1circle2 cubic-bezier(0.7, 0, 0.3, 1) infinite;
  transform-origin: 40% 70%;
}

.act-h .tr--1 svg .cir-anim5 {
  animation: 3s tr1circle2 cubic-bezier(0.7, 0, 0.3, 1) infinite 1s;
  transform-origin: 40% 70%;
}

.act-h .tr--1 svg .cir-anim6 {
  animation: 3s tr1circle3 cubic-bezier(.68, -.55, .27, 2.7) infinite;
  transform-origin: 50% 50%;
}

.act-h .tr--1 .tr--1-sqr {
  animation: 3s tr1circle4 cubic-bezier(.68, -.55, .27, 2.7) infinite 1s;
  transform-origin: 50% 50%;
}

@keyframes tr1circle {
  0% {
    transform: scale(.2);
  }

  20% {
    transform: scale(1);
  }

  100% {
    transform: scale(.2);
  }
}

@keyframes tr1circle2 {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes tr1circle3 {
  0% {
    transform: scale(1);
  }

  15% {
    transform: scale(1.04);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes tr1circle4 {
  0% {
    transform: rotate(0deg);
  }

  15% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.tr--1-sqr {
  position: absolute;
  width: 4vw;
  height: 4vw;
  background: #F3AE3E;
  right: -40vw;
  top: 7vw;
}

.tr--1-plc {
  position: absolute;
  width: 12vw;
  height: 12vw;
  right: -34.2vw;
  bottom: 5vw;
}

.page-pagination {
  position: absolute;
  right: -12vw;
  width: auto;
  height: auto;
}

.page-pagination a {
  text-decoration: none;
}

.page-pagination h2 {
  font-size: calc(23px + 3.8vw);
  line-height: calc(18px + 3.2vw);
  letter-spacing: -0.1vw;
  text-align: right;
  color: #222;
  transition: .4s ease;
  transform-origin: center;
  pointer-events: none;
  -webkit-text-stroke: calc(1px + .1vw) #222;
}

.page-pagination a:hover h2 {
  animation: .5s page-pagination ease;
}

.page-pagination a:nth-child(2):hover h2 {
  color: #F3AE3E;
  -webkit-text-stroke: calc(1px + .1vw) #F3AE3E;
}

.page-pagination a:nth-child(3):hover h2 {
  color: #c54a37;
  -webkit-text-stroke: calc(1px + .1vw) #c54a37;
}

.page-pagination a:nth-child(4):hover h2 {
  color: #47a0cf;
  -webkit-text-stroke: calc(1px + .1vw) #47a0cf;
}

.page-pagination a:nth-child(5):hover h2 {
  color: #F3AE3E;
  -webkit-text-stroke: calc(1px + .1vw) #F3AE3E;
}

@keyframes page-pagination {
  0% {
    transform: rotate(0deg);
  }

  30% {
    transform: rotate(3deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

/* .page-pagination a.active h2{
color: #F3AE3E;
} */

.page-pagination p {
  font-family: "FuturaIWS";
  font-size: 1.3vw;
  line-height: 0;
  font-weight: lighter;
  text-align: right;
  color: #c54a37;
  padding: 0.7vw 0;
}


.scroll-down {
  position: absolute;
  bottom: 6vh;
  left: 12%;
  width: 2.4vw;
  height: 2.4vw;
  min-width: 50px;
  min-height: 50px;
  transform: translate(0, 0);
  text-transform: uppercase;
  font-size: 14px;
  overflow: visible;
  z-index: 7;
  transition: none;
}

.scroll-down svg {
  fill: #333;
}

.scroll-down svg .sr-d {
  fill: #f7f5e9;
  animation: 1.5s scrollDown infinite ease;
}

@keyframes scrollDown {
  0% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateY(20%);
    opacity: 0;
  }
}

.pre-anim.before-act .scroll-down {
  opacity: 0;
}

.pre-anim.act .scroll-down {
  opacity: 1;
  transition: 1s ease-out 0.2s;
}

.page-nav {
  position: absolute;
  right: 12.5%;
  width: auto;
  height: 100vh;
  padding: 2vh 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.page-nav-a {
  display: flex;
  flex-direction: column;
  pointer-events: painted;
}

.page-nav-a a {
  width: 1.1vw;
  height: 1.1vw;
  min-width: 30px;
  min-height: 30px;
  display: flex;
  margin: 5px 0;
  font-size: 0%;
  position: relative;
  justify-content: center;
  align-items: center;
}

.page-nav-a a::after {
  content: "";
  position: absolute;
  width: 50%;
  height: 50%;
  min-height: 5px;
  transition: 0.5s ease-out;
  border-radius: 50%;
  transform: scale(0.6);
  background: #222;
}

.page-nav-a a::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 90%;
  min-height: 5px;
  transition: 0.5s ease-out;
  border-radius: 50%;
  transform: scale(0);
  background: #f7f5e9;
  transition: .5s cubic-bezier(.68, -.55, .27, 1.7);
}

.page-nav-a a.active,
.page-nav-a a.active::after,
.page-nav-a a.active::before,
.page-nav-a a:hover::after,
.page-nav-a a:hover::before {
  transform: scale(1);
}

.page-nav-a a.active::after {
  background: #F3AE3E;
}


#sectionPin {
  height: 100vh;
  overflow: hidden;
  display: flex;
  position: relative;
  width: 100%;
}


.f-dekstop {
  height: 100vh;
  /* min-width: 280vw; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 2vw;
  user-select: none;
}

a {
  text-decoration: none;
}

.f-dekstop .img-hor {
  width: 54vh;
  height: 54vh;
  position: relative;
  overflow: hidden;
  margin: 0 2.3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: none;
  z-index: 2;
  /* border-radius: calc(15px + .3vw); */
  /*transition: .4s cubic-bezier(0.7, 0, 0.3, 1) .2s; */
}

.f-dekstop .img-cont-border {
  width: 54vh;
  height: 54vh;
  position: absolute;
  background: #EBDDBD;
  top: 2vw;
  left: 2vw;
  margin: 0 2.3vw;
  z-index: 1;
  transform: scale(1);
  pointer-events: none;
  /* border-radius: calc(15px + .3vw); */
}

/* .f-dekstop .img-hor:hover {
  border-radius: 0%;
} */

.f-dekstop div:nth-child(1) .img-hor,
.f-dekstop div:nth-child(1) .img-cont-border {
  margin: 0 2.3vw 0 15vw;
}

.f-dekstop .img-hor::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0;
  transition: .4s cubic-bezier(0.7, 0, 0.3, 1) .2s;
  pointer-events: none;
}

.f-dekstop div:nth-child(1) .img-hor::before {
  background: #c54a37;
}

.f-dekstop div:nth-child(2) .img-hor::before {
  background: #212347;
}

.f-dekstop div:nth-child(3) .img-hor::before {
  background: #47a0cf;
}

.f-dekstop div:nth-child(4) .img-hor::before {
  background: #F3AE3E;
}

.f-dekstop div:nth-child(5) .img-hor::before {
  background: #c54a37;
}

.f-dekstop a .img-hor {
  opacity: 1;
  transition: .4s ease;
}

.f-dekstop a .img-hor::before {
  background: none;
}

.f-dekstop a:hover .img-hor {
  background: none;
  opacity: 0;
}

.f-dekstop a .img-hor h2 {
  -webkit-text-stroke: calc(1px + .1vw) #222;
  color: #222;
}

.f-dekstop a .img-hor:hover::before {
  background: none;
  opacity: 0;
}

.f-dekstop .img-hor:hover::before {
  opacity: 1;
  transition: .7s cubic-bezier(0.7, 0, 0.3, 1);
}

.f-dekstop .img-hor .img-cont {
  transform: scale(1);
  transition: 0.8s ease-in-out;
  cursor: none;
}

.f-dekstop .img-hor:hover .img-cont {
  transform: scale(1.2);
}

.f-dekstop .img-hor a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.f-dekstop .skewElemPrlx {
  position: absolute;
  width: 70%;
  z-index: 2;
  pointer-events: none;
  opacity: 0;
  transform: scale(1.1);
  transition: .4s ease-in;

}

.f-dekstop .skewElemPrlx h3 {
  color: #f7f5e9;
  font-size: 3.7vw;
  line-height: 3vw;
  letter-spacing: -.2vw;
  text-align: left;
  -webkit-text-stroke: calc(1px + .05vw) #f7f5e9;
}

.f-dekstop .skewElemPrlx p {
  color: #f7f5e9;
  text-align: left;
}

.f-dekstop .skewElemPrlx hr {
  background: #f7f5e9;
  left: 0;
  transform: translateX(0);
}

.f-dekstop .img-hor:hover .skewElemPrlx {
  opacity: 1;
  transform: scale(1);
  transition: .4s ease-out .5s;
}

.f-dekstop .category {
  color: #c54a37;
  position: absolute;
  z-index: 2;
  background: #f7f5e9;
  top: 10%;
  right: 15%;
  font-size: .6vw;
  opacity: 0;
  transition: .4s ease-in;
}

.f-dekstop .img-hor:hover .category {
  opacity: 1;
  transition: .4s ease-out .5s;
}

.f-dekstop div:nth-child(1) .img-hor .category {
  color: #c54a37;
}

.f-dekstop div:nth-child(2) .img-hor .category {
  color: #212347;
}

.f-dekstop div:nth-child(3) .img-hor .category {
  color: #47a0cf;
}

.f-dekstop div:nth-child(4) .img-hor .category {
  color: #f6c04b;
}

.f-dekstop div:nth-child(5) .img-hor .category {
  color: #c54a37;
}


.f-mobile {
