.go-tt-page {
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  z-index: 2;
  font-family: "FuturaIWS";
}

.go-tt-page a {
  position: absolute;
  bottom: 3.5vw;
