.header-bl {
  position: relative;
  top: 0;
  left: 0;
  height: 11vw;
  min-height: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-bl h2 {
  text-align: left;
  top: 2vw;
}

.header-img {
  position: absolute;
  width: 50%;
  height: 100%;
  top: -5%;
  right: -5%;
  pointer-events: none;
  transform: scale(1.2);
  opacity: 0;
}

.container-bl {
  opacity: 0;
}

.blog-navigation,
.card-content-container a,
.card-image-container,
.card-content {
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}

.blog-list {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 3vw;
}

.blog-list .card {
  width: 42%;
  margin: 0 3% 2% 3%;
}

.card-content-container {
  position: relative;
}

.card-content.c-c-back::before{
  content: '';
  position: absolute;
  top: 6%;
  left: 6%;
  width: 100%;
  height: 100%;
  background: #EBDDBD;
}

.card-content-container h2 {
  width: 90%;
  left: 10%;
}

.card-content-container .page-content h3 {
  text-align: left;
}

.blog-list .card-content h3 {
  text-align: left;
  font-size: calc(20px + 2vw);
  line-height: calc(18px + 1.7vw);
  margin: 10px 0;
}

.card-content-container a {
  color: #c54a37;
  transition: .3s ease;
  text-decoration: underline;
}

.card-content-container a:hover {
  color: #F3AE3E;
  text-decoration: none;
}

.link-img-cont {
  position: absolute;
  width: 70%;
  height: 100%;
  display: block;
  top: 0;
  left: 15%;
  z-index: 2;
  user-select: none;
}

.blog-navigation {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0 0 calc(20px + 2vw) 0;
}

.blog-navigation div {
  position: relative;
  color: #222;
  cursor: pointer;
  background: transparent;
  padding: calc(10px + 0.5vw) calc(15px + 0.8vw);
  margin: calc(5px + .5vw) calc(5px + .5vw);
  text-transform: uppercase;
  font-size: calc(14px + .8vw);
  font-family: "FuturaIWS";
}

.blog-navigation div::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #c54a37;
  border-radius: 15vw;
  z-index: -1;
  transform: scale(1.1);
  opacity: 0;
  transition: transform .5s cubic-bezier(.68, -.55, .27, 1.7), opacity .5s ease;
}

