} */

.blog-navigation div.active {
  color: #f7f5e9;
}

.blog-navigation div.active::before {
  opacity: 1;
  background: #c54a37;
  transform: scale(1);
  transition: transform .6s cubic-bezier(.68, -.55, .27, 1.7), opacity 1s ease;
}


.card-anim {
  opacity: 0;
  animation: 0.3s identifier ease-out forwards;
  animation-delay: 0.5s;
}

@keyframes identifier {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.card-image-container {
  position: relative;
  width: 100%;
  height: 28vw;
  min-height: 230px;
  background: #EBDDBD;
  overflow: hidden;
  /* border-radius: .7vw; */
}

.card.card-image-container {
  left: 10%;
}

.card-image-container .card-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #EBDDBD;
  transform: scale(1);
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);

}

.card-image-container a {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: none;
}

.card-image-container:hover .card-image {
  transform: scale(1.2);
}

.card-image-container .title-text {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0 15%;
  z-index: 3;
  background: #c54a37;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
  color: #f7f5e9;
  opacity: 0;
}

.card:nth-child(2n + 2) .title-text {
  background: #212347;
}

.card:nth-child(3n + 3) .title-text {
  background: #F3AE3E;
}

.card-image-container:hover .title-text {
  visibility: visible;
  opacity: 1;
}

.card-image-container .title-text h3 {
  color: #f7f5e9;
  font-size: 3.3vw;
  line-height: 2.7vw;
  letter-spacing: -0.03vw;
  text-align: left;
  -webkit-text-stroke: calc(1px + .05vw) #f7f5e9;
}

.card-image-container .title-text hr {
  background: #f7f5e9;
  left: 0;
  transform: translateX(0);
}

.card-image-container .title-text div {
  transform: scale(1.1);
  opacity: 0;
  transition: 0.6s cubic-bezier(0.7, 0, 0.3, 1);
}

.card-image-container:hover .title-text div {
  transform: scale(1);
  opacity: 1;
  transition: 1.2s cubic-bezier(0.7, 0, 0.3, 1);
}

.img-cont-anim {
  width: 100%;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  margin-top: -50vh;
  z-index: 5;
}

