.title-category {
  background: #f7f5e9;
  color: #c54a37;
  position: absolute;
  z-index: 5;
  padding: 15px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 5%;
  right: 10%;
  user-select: none;
  visibility: hidden;
  opacity: 0;
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
  text-transform: uppercase;
  border-radius: 100px;
  pointer-events: none;
  user-select: none;
  font-size: .6vw;
}

.card-image-container:hover .title-category {
  visibility: visible;
  opacity: 1;
}

.card:nth-child(2n + 2) .title-category {
  color: #212347;
}

.card:nth-child(3n + 3) .title-category {
  color: #F3AE3E;
}

.load-more-btn {
  margin: 0 0 calc(60px + 4vw) 0;
}

.dekstop-load-more {
  cursor: pointer;
  user-select: none;
  transition: 0.4s ease;
  opacity: 1;
}

.dekstop-load-more:hover {
  opacity: 0;
}

.category {
  background: #c54a37;
  padding: 15px 25px;
  color: #f7f5e9;
  text-transform: uppercase;
  border-radius: 100px;
  pointer-events: none;
  user-select: none;
}

.content-pagination {
  position: relative;
  width: 100%;
  margin: calc(40px + 5vw) 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.content-pagination a {
  display: inline-block;
  text-decoration: none;
  color: #222;
  transition: 0.4s ease;
  user-select: none;
}

.content-pagination a h2 {
  color: #222;
  cursor: none;
  left: 0;
}

.content-pagination a h4 {
  cursor: none;
  color: #222;
  font-size: 3vw;
}

.content-pagination a:hover {
  opacity: 0;
}

.test-text {
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
}

.test-text p {
  width: 100%;
}

.cont-img-prlx {
  position: relative;
  width: 100%;
  min-height: 80vh;
  margin: 4vw 0 7vw 0;
  overflow: hidden;
  background: #EBDDBD;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  padding: 0 17%;
}

.cont-img-prlx img {
  pointer-events: none;
  position: absolute;
  top: -10%;
  left: -10%;
  width: 120%;
  height: 120%;
  object-fit: cover;
  user-select: none;
  transform: translateY(-30%) scale(1.3);
}

.cont-img-prlx h2,
.card-content-container .cont-img-prlx h3,
.cont-img-prlx p,
.cont-img-prlx h4 {
  color: #f7f5e9;
  position: relative;
  z-index: 2;
  text-align: right;
}

.c-img-box {
  margin: calc(15px + 2vw) 0 0 0;
  position: relative;
  width: 100%;
  height: 43.5vw;
  /* height: auto; */
  top: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
  user-select: none;
  background-color: #EBDDBD;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: calc(10px + .3vw); */
}

/* .c-img-box::before{
  content: 'image load...';
  color: #222;
} */

.c-img-box img {
  position: absolute;
  /* position: relative; */
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  object-fit: cover;
  user-select: none;
  opacity: 0;
  transform: scale(1.05);
}

.c-img-box img.img-loaded {
  opacity: 1;
  transform: scale(1);
  transition: .5s cubic-bezier(0.7, 0, 0.3, 1);
}

.cont-scrll-txt {
  margin: calc(30px + 4vw) 0;
}

