@media screen and (max-width: 1300px) {

  /* .card-image-container{
    border-radius: 10px;
  } */
  
  .card-image-container:hover img {
    transform: scale(1) translateX(0%);
  }

  .c-img-box {
    height: 51.5vw;
  }

  .blog-list .card-content h3 {
    text-align: left;
    font-size: calc(7px + 1.7vw);
    line-height: calc(6px + 1.4vw);
    letter-spacing:normal;
    margin: 10px 0 0 0;
  }

}
@media screen and (max-width: 900px) {

  .blog-list .card-content h3 {
    font-size: calc(12px + 1.7vw);
    line-height: calc(11px + 1.4vw);
  }

  .blog-list .card {
    width: 46%;
    margin: 0 1.8%;
  }
  .card-image-container{
    height: 35vw;
  }

}

@media screen and (max-width: 768px) {
  .card-content-container h2 {
    width: 100%;
    left: 0;
  }

  .c-img-box {
    height: auto;
    min-height: 300px;
  }

  .c-img-box img {
    position: relative;
  }

  .card-image-container {
    height: 30vw;
    width: 100%;
  }

  .load-more-btn {
    font-size: 16px;
    line-height: 16px;
  }

  .load-more-btn:hover::before {
    background: #f9f9f9;
    transform: scale(1);
  }

  .content-pagination a:hover {
    color: #bd9165;
    opacity: 1;
  }

  .card-content-container h2 {
    text-align: right;
  }

  .card-content {
    width: 100%;
  }

  .header-img {
    width: 80%;
  }

  .content-pagination a h4 {
    cursor: none;
    color: #222;
    font-size: 30px;
  }

  .blog-list .card {
    width: 46.2%;
  }

  .category {
    font-size: 11px;
  }

  .blog-navigation {
    display: none;
  }

  .blog-list .card-content h3 {
    font-size: calc(14px + 1.7vw);
    line-height: calc(12px + 1.4vw);
    /* text-transform: capitalize; */
  }

  .card-image-container{
    height: 37vw;
  }

}

@media screen and (max-width: 500px) {

  .content-pagination a:nth-child(1) h2,
  .content-pagination a:nth-child(2) h4,
  .content-pagination a:nth-child(3) h2{
    width: 55px;
    height: 55px;
    border-radius: 50%;
    font-size: 0px;
    line-height: 0;
    letter-spacing: 0;
  }

  .blog-list .card-content h3 {
    font-size: calc(17px + 1.7vw);
    line-height: calc(16px + 1.4vw);
    margin: 10px 0 30px 0
  }

  .content-pagination a:nth-child(1) h2{
    background: #222;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 24 24'%3E%3Cpolygon fill='%23f7f5e9' points='15.3,2.8 6.1,12 15.3,21.2 17.1,19.5 9.5,12 17.1,4.5 '/%3E%3C/svg%3E%0A");   
    background-position: 50%;
    background-size: 50%;
    background-repeat: no-repeat;
  }
  .content-pagination a:nth-child(2) h4{
    width: 45px;
    height: 45px;
    background: #c54a37;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 24 24'%3E%3Cpolygon fill='%23f7f5e9' points='21.1,11.8 11.9,2.6 2.7,11.8 4.4,13.6 11.9,6 19.4,13.6 '/%3E%3Cpolygon fill='%23f7f5e9' points='21.1,19.6 11.9,10.4 2.7,19.6 4.4,21.4 11.9,13.7 19.4,21.4 '/%3E%3C/svg%3E%0A");
    background-position: 50% 45%;
    background-size: 50%;
    background-repeat: no-repeat;
  }
  .content-pagination a:nth-child(3) h2{
    background: #222;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 24 24'%3E%3Cpolygon fill='%23f7f5e9' points='8.9,21.2 18.1,12 8.9,2.8 7.1,4.5 14.7,12 7.1,19.5 '/%3E%3C/svg%3E%0A");
    background-position: 50%;
    background-size: 50%;
    background-repeat: no-repeat;
  }

  .content-pagination a:hover:nth-child(1) h2,
  .content-pagination a:hover:nth-child(2) h4,
  .content-pagination a:hover:nth-child(3) h2{
    transform: scale(1.15);
    transition: .4s ease;
  }

  .cont-img-prlx {
    min-height: 70vh;
  }
  .blog-list .card {
    width: 100%;
    margin: 0;
  }

  .card-image-container {
    width: 100%;
  }

  .card-content {
    width: 100%;
  }

  .c-img-box {
    height: auto;
    min-height: 200px;
  }

  .header-img {
    width: 90%;
  }

  .blog-list .card-content h3 {
    text-align: center;
  }

  .card-image-container{
    height: 60vw;
  }


}