.theme-app {
  width: 2vw;
  height: 2vw;
  min-width: 40px;
  min-height: 40px;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: painted;
  cursor: pointer;
  z-index: 5;
  transition: background-color 0.6s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: none;
  border-radius: 50%;
  background-color: #f7f5e9;
}

