.navigation {
  position: fixed;
  width: 100%;
  height: auto;
  /* background: rgba(255, 255, 255, 0.2); */
  z-index: 98;
  display: flex;
  align-items: center;
  padding: 3% 12%;
  justify-content: space-between;
  pointer-events: none;
  top: 0;
  left: 0;
}


.navigation .active,
.navigation .active .logo {
  pointer-events: none;
}

.logo {
  width: 2.5vw;
  height: 2.5vw;
  min-width: 45px;
  min-height: 45px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  pointer-events: painted;
  cursor: none;
}

.logo::before,
.logo::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: transform .7s cubic-bezier(.68, -.55, .27, 1.7), background-color .4s ease;
}

.logo::before {
  background-color: #c54a37;
}

.logo:hover::before,
.logo.act::before {
  background-color: #c54a37;
  transform: scale(1.3);
}

.logo svg {
  position: absolute;
  width: 90%;
  height: 90%;
  fill: #f7f5e9;
  transition: transform .7s cubic-bezier(.68, -.55, .27, 1.7) .2s;
}

.logo:hover svg,
.logo.act svg {
  transform: rotate(-45deg);
}

.nm-btn {
  position: relative;
  width: 2.7vw;
  height: 2.7vw;
  min-width: 45px;
  min-height: 45px;
  z-index: 3;
  border-radius: 50%;
  cursor: pointer;
  pointer-events: painted;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nm-btn::before,
.nm-btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #222;
  border-radius: 50%;
  transition: .7s cubic-bezier(.68, -.55, .27, 1.7);
}

.nm-btn::before {
  background: #222;
}

.nm-btn::after {
  background: #f7f5e9;
  z-index: -1;
  transform: scale(1.2);
}

.nm-btn:hover::before,
.nm-btn.act::before {
  transform: scale(1.3);
}

.nm-btn:hover::after {
  transform: scale(1);
}

.nm-btn svg {
  fill: #f7f5e9;
  position: absolute;
  width: 50%;
  height: 50%;
}

.nm-btn path:nth-child(1) {
  transform: translate(0, 4%) rotate(0deg);
  transform-origin: center;
  transition: transform 0.8s cubic-bezier(.68, -.55, .27, 1.7);
}

.nm-btn path:nth-child(2) {
  transform: translate(0, -4%) rotate(0deg);
  transform-origin: center;
  transition: transform 0.8s cubic-bezier(.68, -.55, .27, 1.7);
}

.nm-btn.no-act path:nth-child(1),
.nm-btn.no-act path:nth-child(2) {
  transition: none;
}

.nm-btn.act path:nth-child(1) {
  transform: rotate(225deg) translateY(25%);
  transition: transform 0.9s cubic-bezier(.68, -.55, .27, 1.7) 0.2s;
}

.nm-btn.act path:nth-child(2) {
  /* transform: translateX(50%); */
  transform: rotate(-225deg) translate(0%, -26%);
  transition: transform 0.9s cubic-bezier(.68, -.55, .27, 1.7) 0.2s;
}

.nav-menu {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  position: fixed;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateX(118%) skewX(10deg);
  transition: 0.1s cubic-bezier(0.7, 0, 0.3, 1);
  transform-origin: left;
}

.nav-menu .st0 {
  fill: #c54a37;
}

.nav-menu .st1 {
  fill: #F3AE3E;
}

.nav-menu .st2 {
  fill: #EBDDBD;
}

.nav-menu .st3 {
  fill: #47a0cf;
}

.nav-menu .st4 {
  fill: #212347;
}

.nav-menu::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #c54a37;
  z-index: -1;
}

.nav-menu::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f7f5e9;
  transform: translateX(50%) skewX(10deg);
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.act {
  transform: translateX(0%);
  visibility: visible;
  pointer-events: painted;
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.act::before {
  transform: translateX(0%) skewX(0deg);
  transition: 1.3s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.act-left {
  transform: translateX(120%) skewX(-10deg);
  visibility: visible;
  pointer-events: painted;
  transition: 1.1s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.act-left::before {
  transform: translateX(50%) skewX(-10deg);
  transition: 1s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.no-act,
.nav-menu.no-act::before {
  transition: none;
}

.nav-menu-l,
.nav-menu-c {
  width: 100%;
  height: 110%;
  position: relative;
  top: -5%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-menu-l label,
.nav-menu-c-a label {
  font-size: 1.3vw;
  line-height: 0;
  font-weight: lighter;
  position: relative;
  width: 100%;
  color: #222;
  bottom: 1vw;
  font-family: "FuturaIWS";
  opacity: 0;
  overflow: hidden;
}

.nav-menu-l ul {
  text-align: right;
}

.nav-menu-c {
  overflow: hidden;
  color: #f9f9f9;
}

.nav-menu-c::before {
  content: "";
  width: .4vw;
  height: 15%;
  position: absolute;
  background: #222;
  left: 0;
  border-radius: 50px;
  transition: .3s ease .3s;
  opacity: 0;
}

.nav-menu.act .nav-menu-c::before{
opacity: 1;
transition: .7s ease 1s;  
}

.nav-menu-c-b {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  background-color: #EBDDBD;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-menu-c .nav-menu-c-a {
  position: relative;
  top: 0;
  left: 0;
  padding: 0 20%;
}

.nav-menu-c-a h4 {
  color: #c54a37;
  letter-spacing: -0.05vw;
}

.nav-menu-c-a .nav-menu-c-a-d {
  /* transform: translateX(50%); */
  opacity: 0;
}

.nav-menu-c-a div p {
  user-select: text;
  color: #222;
}

.nav-menu-c-a div a {
  color: #222;
  text-decoration: none;
}

.nav-menu-l ul li {
  margin: .4vh 0;
  list-style-type: none;
  text-align: right;
  transform: translateX(35%);
  opacity: 0;
}

.nav-menu-l ul li a {
  display: block;
  text-decoration: none;
  font-size: 5.4vw;
  line-height: 4.6vw;
  position: relative;
  transition: 0.5s ease;
  cursor: pointer;
  z-index: 2;
  overflow: hidden;
  width: 110%;
}

.nav-menu-l ul li a h2 {
  letter-spacing: -0.2vw;
  transition: 0.5s ease;
  color: #222;
  -webkit-text-stroke: calc(2px + .15vw) #222;
  left: -8%;
}

.nav-menu-l ul li a.active h2 {
  /* color: #f6c04b; */
  color: #c54a37;
  -webkit-text-stroke: calc(2px + .15vw) #c54a37;
}

.nav-menu-l ul li a span:nth-child(1),
.nav-menu-l ul li a span:nth-child(2),
.nav-menu-l ul li a span:nth-child(3),
.nav-menu-l ul li a span:nth-child(4),
.nav-menu-l ul li a span:nth-child(5),
.nav-menu-l ul li a span:nth-child(6),
.nav-menu-l ul li a span:nth-child(7) {
  position: relative;
  left: 0%;
}

.nav-menu-l ul li a:hover span:nth-child(1) {
  animation: 0.4s navmenul ease;
}

.nav-menu-l ul li a:hover span:nth-child(2) {
  animation: 0.4s navmenul ease 0.1s;
}

.nav-menu-l ul li a:hover span:nth-child(3) {
  animation: 0.4s navmenul ease 0.2s;
}

.nav-menu-l ul li a:hover span:nth-child(4) {
  animation: 0.4s navmenul ease 0.3s;
}

.nav-menu-l ul li a:hover span:nth-child(5) {
  animation: 0.4s navmenul ease 0.4s;
}

.nav-menu-l ul li a:hover span:nth-child(6) {
  animation: 0.4s navmenul ease 0.5s;
}

.nav-menu-l ul li a:hover span:nth-child(7) {
  animation: 0.4s navmenul ease 0.6s;
}

@keyframes navmenul {
  0% {
    left: 0%;
    color: #222;
  }

  25% {
    left: 8%;
    color: #f6c04b;
    -webkit-text-stroke: calc(2px + .15vw) #f6c04b;
  }

  100% {
    left: 0%;
    color: #222;
  }
}

@keyframes navmenul2 {
  0% {
    left: 0%;
    color: #f7f5e9;
  }

  25% {
    left: 8%;
    color: #f6c04b;
    -webkit-text-stroke: calc(2px + .15vw) #f6c04b;
  }

  100% {
    left: 0%;
    color: #f7f5e9;
  }
}

.nav-menu-l ul li img {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 1;
}

.nav-menu-l ul li.act img {
  display: block;
}

.soc-icon-box {
  position: relative;
  display: flex;
  flex-direction: row;
}

.soc-icons {
  width: 2.2vw;
  height: 2.2vw;
  min-width: 45px;
  min-height: 45px;
  border-radius: 50%;
  margin-right: 1vw;
  margin-top: .3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: transform 0.4s ease;
}

.soc-icons svg {
  position: absolute;
  width: 50%;
  height: 50%;
  fill: #f7f5e9
}

.soc-icons::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #222;
  border-radius: 50%;
  transition: 0.4s ease;
  z-index: -1;
}

.soc-icons:hover::before {
  background: #f6c04b;
  transform: scale(1.2);
}

.soc-icons.email-icon {
  display: none;
}

.soc-icons.email-icon::before{
  background-color: #c54a37;
}

.c-right {
  position: absolute;
  padding: 20px 0;
  bottom: 0;
  color: #222;
  width: 100%;
  text-align: center;
  z-index: 2;
}

.c-right p {
  font-size: 16px;
  line-height: 16px;
}

@media screen and (max-width: 1024px) {

  .nav-menu-l ul li a h2 {
    left: 0;
  }

  .logo:hover::before {
    background: #c54a37;
  }

  .navigation {
    padding: 5% 10%;
  }

  .nav-menu-l ul li {
    margin: 1.4vh 0;
  }

  body.act {
    overflow: hidden;
  }

  .nm-btn:hover::after,
  .logo:hover::after {
    transform: scale(1.2);
  }

  .logo:hover svg {
    transform: none;
  }

  .nm-btn:hover::before,
  .logo:hover::before {
    transform: scale(1);
  }

  .nm-btn.act::before {
    transform: scale(1.3);
  }

  .logo:hover .log-1,
  .logo:hover .log-2,
  .logo:hover .log-4,
  .logo:hover .log-5 {
    transition: none;
    transform: translate(0, 0);
  }


  .nav-menu {
    transform: translateX(118%) skewX(0deg);
  }

  .nav-menu::before {
    transform: translateX(50%) skewX(0deg);
    ;
  }


  .nav-menu.act-left {
    transform: translateX(100%) skewX(0deg);
  }

  .nav-menu.act-left::before {
    transform: translateX(50%) skewX(0deg);
  }

  .nav-menu-l ul li a:hover span:nth-child(n), body.white .nav-menu-l ul li a:hover span:nth-child(n){
    animation: none;
  }

}


@media screen and (max-width: 768px) {

  .nav-menu-l label,
  .nav-menu-c-a label {
    font-size: 17px;
  }

  h2 {
    text-align: center;
  }

  .navigation {
    padding: 40px 50px;
  }

  .nav-menu {
    flex-direction: column;
  }

  .nav-menu-c {
    height: 30%;
  }

  .nav-menu-l {
    padding-top: 20%;
    text-align: center;
  }

  .nav-menu-l ul li a {
    width: 100%;
  }

  .nav-menu-l ul li a h2 {
    font-size: calc(45px + 4vw);
    line-height: calc(32px + 3.5vw);
  }

  .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
    text-align: center;
  }

  .act .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
  }

  .nav-menu-c-a {
    text-align: center;
  }

  .nav-menu-c-b,
  .nav-menu-c-a div div:nth-child(2),
  .nav-menu-c-a div div:nth-child(3),
  .nav-menu-c-a div div:nth-child(4) h4 {
    display: none;
  }

  .nav-menu-c-a div div:nth-child(4) div {
    display: flex;
  }

  .soc-icons {
    margin: 0 7px;
  }

  .soc-icons.email-icon {
    display: block;
  }

  .nav-menu-c {
    justify-content: center;
    align-items: flex-start;
    padding-top: 20px;
  }

  .nav-menu-c::before {
    width: 40%;
    height: 2px;
    left: auto;
    display: none;
  }

  .nm-btn,
  .logo {
    min-width: 40px;
    min-height: 40px;
  }


  .act .nav-menu-c .nav-menu-c-a {
    transition: transform .7s cubic-bezier(0.24, 1.1, 0.85, 0.99) .8s, opacity .4s ease-in .8s;
  }

  /* .nav-menu-l,
  .nav-menu-c {
    height: 100%;
    top: 0;
  } */

}

@media screen and (max-width: 500px) {

  .navigation {
    padding: 30px 30px;
  }

  .c-right,.logo {
    display: none;
  }

  .theme-app {
    left: 12%;
  }

  .theme-app.white {
    background-size: 40%;
  }

  .theme-app.black {
    background-size: 50%;
  }


  .nav-menu-l ul li {
    margin: 1.3vh 0;
  }

  .nav-menu-l ul li a h2 {
    font-size: calc(45px + 4vw);
    line-height: calc(32px + 3.5vw);
    text-align: center;
  }

  .nav-menu-c {
    height: 35%;
  }

  .soc-icon-box {
    justify-content: center;
    flex-wrap: wrap;
  }

  .soc-icons {
    margin: 5px 7px;
  }

}

@media screen and (max-width: 300px) {

  .nav-menu-c-a {
    padding: 0 10%;
  }

  .soc-icons {
    min-width: 35px;
    min-height: 35px;
  }
}