.circles-cont {
    position: relatiave;
    padding: calc(20px + 4vw) 0;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    pointer-events: none;
    overflow: hidden;
}

.circles {
    position: relative;
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    user-select: none;
    pointer-events: none;
}

.circles-s {
    background: #f7f5e9;
    position: relative;
    z-index: 99;
    width: 4.5vw;
    height: 4.5vw;
    border-radius: 50%;
    margin: 1vw;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(0);
    transition: transform 1.5s ease, background-color .7s ease
}

/* .circles-s{box-shadow:-2px 2px 0 #0c0c0c,-4px 4px 0 #0c0c0c,-6px 6px 0 #0c0c0c} */
@media only screen and (max-width:768px) {
    .circles-s {
        min-width: 10vw;
        min-height: 10vw
    }

    .circles-s:nth-child(1n+21) {
        display: none
    }
}

@media only screen and (max-width:450px) {
    .circles {
        width: 80%;
        padding: 5vh 0
    }

    .circles-s {
        min-width: 15vw;
        min-height: 15vw
    }

    .circles-s:nth-child(2n+13) {
        display: none
    }
}