.wai-cont{position:relative;width:100%;height:100%;min-height:70vh;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;user-select:none;pointer-events:none;overflow:hidden}
.wai-cont h2{text-transform:l;font-size:70px;line-height:60px;font-family:FuturaIWS;color:#222}
.wai-cont .wai-prof h2{font-size:120px;line-height:110px; white-space:nowrap;text-transform:uppercase}
.wai-cont.active .wai-prof h2{animation:5s identifier infinite ease}
.wai-prof{position:relative}
.wai-simbol{position:relative;display:flex;justify-content:center;align-items:center;width:70px;height:70px;background:#f7f5e9;color:#000;border-radius:50%;line-height:0;font-size:40px;font-family:Roboto-Black;margin-top:10px}
@keyframes identifier{
0%{transform:translateX(-7vw) skew(15deg);opacity:0}
25%{transform:translateX(0) skew(0);opacity:1}
85%{transform:translateX(0) skew(0);opacity:1}
100%{transform:translateX(7vw) skew(-10deg);opacity:0}
}
.wai-btn button{width:70px;height:70px;border:3px solid #f7f5e9;background:0 0;border-radius:50%;text-transform:uppercase;cursor:pointer;margin:20px 10px 40px 0;transition:.4s ease;position:absolute;bottom:0;left:50%;transform:translateX(-50%);user-select:none;font-family:FuturaIWS;color:#f7f5e9;pointer-events:painted}
.wai-btn button:hover{background:#f7f5e9;color:#0c0c0c}
.wai-btn button.btn-off{display:none}
@media only screen and (max-width:768px){
.wai-cont .wai-prof h2{font-size:70px;line-height:60px}
}
@media only screen and (max-width:450px){
.wai-cont h2{font-size:30px;line-height:50px}
.wai-cont .wai-prof h2{font-size:40px;line-height:30px}
}